body{
	font-family: $f-o-r;
	font-weight: normal;
	font-size: 16px;
	line-height: 1.3;
	background-color: #fff;
	opacity: 0;
	transition: opacity .3s linear;
	color: $c-bck;
	&.loaded{
		opacity: 1;
	}
}  


.is-desktop{
	.topp__menu{
		li{
			&:hover{
				>.sub-menu{
