}



.topp{
	padding: 20px 100px;
	&__blck{
		&-1{
			display: flex;
			flex-basis: 60%;
			align-items: center;
			justify-content: space-between;
		}
		&-2{
			display: flex;
			flex-basis: 40%;
			align-items: center;
			justify-content: flex-end;
		}
	}
	&__menu{
		list-style-type: none;
		font-size: 12px;
		font-family: $f-o-s;
		text-transform: uppercase;
		z-index: 999 !important;
		.sub-menu{
			display: none;
			width: 220px;
			list-style-type: none;
			z-index: 9;
			padding: 15px 0;
			background-color: $c-yw;
			position: absolute;
			a{
				color: $c-wh;
				display: block;
				padding: 10px 20px;
				&:hover{
					background-color: darken( $c-yw, 10 );
					text-decoration: none;
				}
			}
			li{
				display: block;
				&.current-menu-parent{
					>a{
						background-color: darken( $c-yw, 10 );
					}
				}
				&.current-menu-item{
					>a{
						background-color: darken( $c-yw, 10 );
					}	
				}
			}	
		}
		>li{
			display: inline-block;
			margin-left: 30px;
			position: relative;
			padding: 20px 0;
			&.current_page_ancestor{
				>a{
					border-bottom: 3px solid $c-yw;
				}
			}
			&.current_page_item{
				>a{
					border-bottom: 3px solid $c-yw;
				}	
			}
			&.current-menu-ancestor{
				>a{
					border-bottom: 3px solid $c-yw;
				}	
			}
			>a{
				border-bottom: 3px solid transparent;
			}
			>.sub-menu{
				top: 50px;
				left: 0;
				li{
					.sub-menu{
						top: 0;
						left: 220px;
					}
				}
			}
			&:hover{
				text-decoration: none;
				>a{
					border-bottom: 3px solid $c-yw;
				}
			}
		}
		
		a{
			color: $c-bck;
			&:hover{
				text-decoration: none;
			}
			&:focus{
				text-decoration: none;

			}
		}
	}
	&__soc{
		list-style-type: none;
		li{
			display: inline-block;
			margin: 5px;
		}
		a{
			&:hover{
				opacity: .6;
			}
		}
	}
	&__phone{
		font-size: 14px;	
		font-family: $f-o-e;
		color: $c-bck;
		margin: 0 15px;		
	}
	&__div{
		display: inline-block;
		font-size: 14px;
		margin-right: 10px;
	}
	&__sel{
		text-transform: uppercase;
		font-family: $f-o-s;
		font-size: 12px;
		position: relative;
		cursor: pointer;
		.icon{
			display: none;
		}
		// display: inline-block;
		i{
			font-size: 10px;
			display: inline-block;
			margin-left: 5px;
		}
	}
	&__country{
		margin-left: 15px;
		cursor: pointer;
	}
}//.topp



