.inffo{
	min-height: 690px;
	&__blck{
		flex-basis: 50%;
	}
	&__blck-1{
		display: flex;
		flex-direction: column;
		justify-content: center;
		iframe{
			height: 100%;
			margin: 0;
		}
	}
	&__blck-2{
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
	}
	&__subb{
		flex-basis: 50%;
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
	}
	&__cell{
		flex-basis: 50%;
		display: flex;
		align-items: flex-end;
		flex-direction: row;
		position: relative;
		overflow: hidden;
		&:hover{
			&:after{
				visibility: hidden;
				opacity: 0;
				transform: translateY( 400px );
				transition-delay: .4s;
			}
			&:before{
				visibility: visible;
				opacity: 1;
				transform: translateY( 0px );
			}
		}

		//Green grad
		&:after{
			content: '';
			position: absolute;
			display: block;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			transition: all .4s linear;
			transition-delay: 0s;
			opacity: 1;
			visibility: visible;
			transform: translateY( 0px );

			background: rgba(15,119,121,0);
			background: -moz-linear-gradient(top, rgba(15,119,121,0) 0%, rgba(15,119,121,1) 100%);
			background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(15,119,121,0)), color-stop(100%, rgba(15,119,121,1)));
			background: -webkit-linear-gradient(top, rgba(15,119,121,0) 0%, rgba(15,119,121,1) 100%);
			background: -o-linear-gradient(top, rgba(15,119,121,0) 0%, rgba(15,119,121,1) 100%);
			background: -ms-linear-gradient(top, rgba(15,119,121,0) 0%, rgba(15,119,121,1) 100%);
			background: linear-gradient(to bottom, rgba(15,119,121,0) 0%, rgba(15,119,121,1) 100%);
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0f7779', endColorstr='#0f7779', GradientType=0 );
		}
			
		//Yellow gradient	
		&:before{
			content: '';
			position: absolute;
			display: block;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			transition: all .4s linear;
			opacity: 0;
			transform: translateY( 400px );
			visibility: hidden;
			background: rgba(236,171,73,0.4);
			background: -moz-linear-gradient(top, rgba(236,171,73,0.4) 0%, rgba(187,112,0,1) 100%);
			background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(236,171,73,0.4)), color-stop(100%, rgba(187,112,0,1)));
			background: -webkit-linear-gradient(top, rgba(236,171,73,0.4) 0%, rgba(187,112,0,1) 100%);
			background: -o-linear-gradient(top, rgba(236,171,73,0.4) 0%, rgba(187,112,0,1) 100%);
			background: -ms-linear-gradient(top, rgba(236,171,73,0.4) 0%, rgba(187,112,0,1) 100%);
			background: linear-gradient(to bottom, rgba(236,171,73,0.4) 0%, rgba(187,112,0,1) 100%);
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ecab49', endColorstr='#bb7000', GradientType=0 );
		}

		&-cont{
			color: $c-wh;
			z-index: 9;
			padding: 30px 30px 10px;
			a{
				color: $c-wh;
				text-decoration: underline;
				&:hover{
					text-decoration: none;
				}
			}
			p{
				font-family: $f-o-l;
			}
			b{
				font-size: 24px;
				font-family: $f-g-r;
			}
		}

	}

	&__yw{
		padding: 19px 15px;
		background-color: rgba( $c-yw, 0.8 );
		color: $c-wh;
		font-size: 30px;
		font-family: $f-g-l;
