		}
	}
	&__item{
		flex-basis: 32.7%;
		margin-bottom: 20px;
		display: flex;
		flex-direction: column;
		min-height: 580px;
		position: relative;
		
		

		

		&:after{
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: rgba(0,0,0,0);
			background: -moz-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%);
			background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(0,0,0,0)), color-stop(100%, rgba(0,0,0,1)));
			background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%);
			background: -o-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%);
			background: -ms-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%);
			background: linear-gradient(to bottom, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%);
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#000000', GradientType=0 );
		}

		&:nth-child(odd){
			.blog__img{
				img{
					visibility: hidden;
				}
			}
			.blog__content{
				color: $c-wh;
			}
			.blog__content-read{
				a{
					color: $c-wh;
				}
			}
		}

		&:nth-child(even){
			background-image: none !important;
			&:after{
				display: none;
			}
			.blog__img{
			
			}
			.blog__content{
				background-color: $c-wh;
				color: $c-bck;
				padding-bottom: 50px;
			}
			.blog__content-read{
				position: absolute;
				right: 0;
				bottom: 0;
				margin-bottom: 0;
				a{
					display: inline-block;
					padding: 10px 25px;
					color: $c-wh;
					background-color: $c-yw;
					&:hover{
						text-decoration: none;
						background-color: darken( $c-yw, 10 );
					}
				}
			}
		}

	}
	&__img{
		flex-basis: 45%;
		position: relative;
		z-index: 9;
		img{
			width: 100%;
			height: 290px;
			object-fit: cover;
			margin-bottom: 0;
		}
	}
	&__content{
		flex-basis: 55%;
		position: relative;
		z-index: 9;
		padding: 30px;
		&-title{
			font-size: 30px;
			font-family: $f-g-l;
			margin-bottom: 30px;
		}
		&-txt{
			font-size: 14px;
			// font-family: $f-m-r;
		}
		&-read{
			font-size: 16px;
			font-family: $f-m-r;
		}
	}
	&__foot{
		margin: 0 0 60px;
	}
}//.blog


