		min-height: 640px;
		justify-content: space-between;
	}
	&__sale{
		position: absolute;
		width: 110px;
		height: 110px;
		top: -5px;
		right: -5px;
		background: url('../img/sale.png') center no-repeat;
		span{
			font-size: 16px;
			font-family: $f-o-e;
			display: block;
			transform: rotate(45deg);
			color: $c-wh;
			text-align: center;
	    position: absolute;
	    width: 100%;
	    top: 30%;
	    right: -15%;
	    
		}
	}
	&__it{
		background-color: #f3f3f3;
		flex-basis: 23.5%;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		padding: 40px 50px;
		position: relative;
		&:last-child{
			.deliv__it-price{
				margin-top: 5px;
			}
		}
		&:before{
			content: "";
			top: 50%;
			left: 0;
			height: 0;
			background-color: $c-grn;
			width: 10px;
			position: absolute;
			opacity: 0;
			visibility: hidden;
			transition: all .3s linear;
		}
		&:hover{
			&:before{
				top: 0;
				height: 100%;
				opacity: 1;
				visibility: visible;
			}
			.deliv__it-price{
				opacity: 1;
			}
		}
		
		&.actt{
			&:before{
				top: 0;
				height: 100%;
				opacity: 1;
				visibility: visible;
			}
			.deliv__it-price{
				opacity: 1;
			}
		}

		&-order{
			margin-top: auto;
			.butt{
				display: block;
				text-align: center;
				line-height: 25px;
				&:hover{
					text-decoration: none;
					color: $c-wh;
				}
			}
		}
		&-titl{
			font-family: $f-g-l;
			text-transform: uppercase;
			margin-bottom: 20px;
			h4{
				font-size: 28px;
				margin-top: 0;	
			}
		}
		&-price{
			// background-color: $c-grn;
			background: url('../img/dotted.png');
			padding: 20px;
			border-radius: 3px;
			text-align: center;
			color: $c-wh;
			margin: 20px 0 50px;
			opacity: 0.6;
			// visibility: hidden;
			transition: all .3s linear;
			h5{
				margin: 0;
			}
		}
		&-opt{
			font-size: 14px;
			b{
				font-family: $f-o-b;
			}
			p{
				margin-bottom: 15px;
			}
			ul{
				list-style-type: none;
			}
			li{
				margin-bottom: 15px;
				padding-left: 20px;
				background: url('../img/bird.png') center left no-repeat;
			}
		}
	}
